.generalInput {
    width: 258px;
    height: 56px;
    padding: 0px 15px;
    border: 1px solid grey;
    outline: 3px solid white;
    border-radius: 15px;
    background-color: #ffffff;
    color: grey;
    font-size: 16px;
    z-index: 10;
}

@media screen and (max-width: 500px) {
    .generalInput {
        width: 210px;
    }
}

@media screen and (max-width: 350px) {
    .generalInput {
        width: 190px;


    }
}

.errorInput {
    width: 258px;
    height: 56px;
    padding: 0px 15px;
    border: 1px solid white;
    outline: 3px solid firebrick;
    border-radius: 15px;
    font-size: 16px;
}
@media screen and (max-width: 500px) {
    .errorInput {
        width: 210px;
    }
}

@media screen and (max-width: 350px) {
    .errorInput {
        width: 190px;


    }
}


.inputContainer {
    position: relative;
}

.textError {
    position: absolute;
    bottom: -20px;
    left: 0;
    color: #E43F3F;
}

.closeError {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 13px;
    top: 13px;
    border: 1px solid #E43F3F;
    border-radius: 50%;
    background-color: #E43F3F;
    color: white;
    font-size: 20px;
    text-align: center;
}




