
.modalContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(23,33,70, 0.4) ;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 21;
}

.modalBox {
    position: relative;
    width: 384px;
    height: 536px;
    border-radius: 24px;
    background-color: #FFFFFF;



}

.closeButton {
    width: 30px;
    height: 30px;
    position: absolute;
    right: -10px;
    top: -10px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    background-color: #F2F2F2;
    z-index: 22;
}

@media screen and (max-width: 500px) {
    .modalBox {
        width: 300px;
        height: 500px;
    }
}

@media screen and (max-width: 350px) {
    .modalBox {
        width: 270px;
        height: 500px;
    }
}